import React from 'react'
import PropTypes from 'prop-types'
import { FormBuilder, FormItem } from 'rc-form-pure'

import { ReactComponent as EditIcon } from '../../../../components/ui/icons/edit.svg'
import { TextField } from '../../../../components/ui/form/fields/TextField'
import { SwitchField } from '../../../../components/ui/form/fields/SwitchField'
import { Button } from '../../../../components/ui/Button'

import { Container } from './Container'
import styles from './controls.module.less'

const FIELDS = {
  monthsAmount: { fieldKey: 'MonthsAmount', label: 'Количество мес.' },
  useWeekDayMapping: { fieldKey: 'UseWeekDayMapping', label: 'Учитывать дни недели' },
  useDayTypeMapping: { fieldKey: 'UseDayTypeMapping', label: 'Учитывать типы дней' },
}

const BOOL_LABELS = {
  true: 'ON',
  false: 'OFF',
}

export const CalibrationSettings = ({ isActive, setActiveSettings, data, onSubmit }) => {
  const handleSubmit = (values, errors) => {
    if (!errors) {
      onSubmit(values)
      setActiveSettings()
    }
  }

  const monthsAmountInitialValue = data[FIELDS.monthsAmount.fieldKey]
  const useWeekDayMappingInitialValue = data[FIELDS.useWeekDayMapping.fieldKey]
  const useDayTypeMappingInitialValue = data[FIELDS.useDayTypeMapping.fieldKey]

  return (
    <Container isActive={isActive}>
      <span className={styles.title} role="button" onClick={setActiveSettings}>
        Настройка калибровки <EditIcon className={styles.titleIcon} />
      </span>

      {isActive ? (
        <FormBuilder withForm onSubmit={handleSubmit}>
          <FormItem
            name={FIELDS.monthsAmount.fieldKey}
            label={FIELDS.monthsAmount.label}
            type="number"
            underline
            labelSize="small"
            initialValue={monthsAmountInitialValue}
            component={TextField}
          />
          <FormItem
            name={FIELDS.useWeekDayMapping.fieldKey}
            label={FIELDS.useWeekDayMapping.label}
            labelSize="small"
            initialValue={useWeekDayMappingInitialValue}
            component={SwitchField}
          />
          <FormItem
            name={FIELDS.useDayTypeMapping.fieldKey}
            label={FIELDS.useDayTypeMapping.label}
            labelSize="small"
            initialValue={useDayTypeMappingInitialValue}
            component={SwitchField}
          />

          <div className={styles.submitContainer}>
            <Button block type="primary" size="large" className="asd">
              Сохранить
            </Button>
          </div>
        </FormBuilder>
      ) : (
        <div className={styles.values}>
          <label className={styles.label}>{FIELDS.monthsAmount.label}</label>
          <span className={styles.value}>{monthsAmountInitialValue}</span>

          <label className={styles.label}>{FIELDS.useWeekDayMapping.label}</label>
          <span className={styles.value}>{BOOL_LABELS[useWeekDayMappingInitialValue]}</span>

          <label className={styles.label}>{FIELDS.useDayTypeMapping.label}</label>
          <span className={styles.value}>{BOOL_LABELS[useDayTypeMappingInitialValue]}</span>
        </div>
      )}
    </Container>
  )
}

CalibrationSettings.propTypes = {
  onSubmit: PropTypes.func.isRequired,
  isActive: PropTypes.bool,
  setActiveSettings: PropTypes.func.isRequired,
  data: PropTypes.shape({
    [FIELDS.monthsAmount.fieldKey]: PropTypes.number,
    [FIELDS.useWeekDayMapping.fieldKey]: PropTypes.bool,
    [FIELDS.useDayTypeMapping.fieldKey]: PropTypes.bool,
  }),
}

CalibrationSettings.defaultProps = {
  data: {},
}
