import React, { useCallback, useReducer } from 'react'
import { createReducer } from '@reduxjs/toolkit'
import { useSelector, useDispatch } from 'react-redux'

import { gtpSelector } from '../../../selectors/gtp'
import { saveGtpAction } from '../../../store/slices/gtpSlice'

import { CalibrationSettings } from './controls/CalibrationSettings'
import { Indicators } from './controls/Indicators'
import { Name } from './controls/Name'
import { OremSettings } from './controls/OremSettings'
import { RreSettings } from './controls/RreSettings'
import { VoltageLevels } from './controls/VoltageLevels'
import styles from './SideBar.module.less'

const BLOCKS = {
  calibration: 'calibration',
  name: 'name',
  rreSettings: 'rreSettings',
  voltageLevels: 'voltageLevels',
  oremSettings: 'oremSettings',
  indicators: 'indicators',
}
const initialActiveSettings = {}
const settingsReducer = createReducer(initialActiveSettings, {
  toggle: (state, action) => {
    const prevState = state[action.payload]
    state = {}
    // clear
    state[action.payload] = !prevState
    return state
  },
})

export const SideBar = () => {
  const dispatch = useDispatch()
  const selectedGtp = useSelector(gtpSelector)
  const [activeSettings, setActiveSettings] = useReducer(settingsReducer, initialActiveSettings)

  const activateSettings = blockKey => {
    return useCallback(() => {
      return setActiveSettings({ type: 'toggle', payload: blockKey })
    }, [blockKey])
  }

  const onSubmit = updates => {
    dispatch(saveGtpAction({ ...selectedGtp, ...updates }))
  }

  const getBaseProps = key => ({
    isActive: activeSettings[key],
    setActiveSettings: activateSettings(key),
    onSubmit,
  })

  return (
    <div className={styles.container}>
      <Name {...getBaseProps(BLOCKS.name)} name={selectedGtp.Name} />

      <RreSettings {...getBaseProps(BLOCKS.rreSettings)} data={selectedGtp} />

      <VoltageLevels {...getBaseProps(BLOCKS.voltageLevels)} transferPriceTable={selectedGtp.TransferPriceTable} />

      <OremSettings {...getBaseProps(BLOCKS.oremSettings)} data={selectedGtp} />

      <CalibrationSettings
        {...getBaseProps(BLOCKS.calibration)}
        data={selectedGtp?.Preferences?.CalibrationPreference}
      />

      <Indicators {...getBaseProps(BLOCKS.indicators)} />
    </div>
  )
}
