import React, { useMemo } from 'react'
import { useDispatch, useSelector } from 'react-redux'
import { Menu } from 'antd'

import { setCurrentGtpAction } from '../../store/slices/gtpSlice'
import { ReactComponent as MenuBoxIcon } from '../ui/icons/menu-box.svg'
import { ReactComponent as LogoPpIcon } from '../ui/icons/logo-pp.svg'
import { Button } from '../ui/Button'
import { gtpListSelector, gtpSelector } from '../../selectors/gtp'
import { Dropdown } from '../ui/Dropdown'

import styles from './Header.module.less'

export const Header = () => {
  const dispatch = useDispatch()
  const currentGtp = useSelector(gtpSelector)
  const gtpList = useSelector(gtpListSelector)

  const menu = useMemo(
    () => (
      <Menu>
        {gtpList.map(gtp => (
          <Menu.Item key={gtp.Id} onClick={() => dispatch(setCurrentGtpAction(gtp))}>
            {gtp.Name}
          </Menu.Item>
        ))}
      </Menu>
    ),
    [dispatch, gtpList]
  )

  return (
    <header className={styles.container}>
      <nav>
        <ul>
          <li>
            <a>
              <MenuBoxIcon />
            </a>
            {/* <div> */}
            <LogoPpIcon className={styles.logo} />
            {/* </div> */}
          </li>

          <li>
            <div className={styles.objectSplitter} />
            <Dropdown overlay={menu}>
              <Button type="link">
                <span className={styles.objectBtn}>{currentGtp.Name}</span>
              </Button>
            </Dropdown>
            {/* <svg>
                <use href="#arrowPopup.svg" />
              </svg> */}
          </li>
        </ul>
      </nav>
    </header>
  )
}
