import axios from 'axios'
import qs from 'qs'

export const axiosInstance = axios.create({
  // baseURL: 'https://dev.enrsoft.ru/rest/calculator',
  baseURL: 'http://localhost:8000/rest/calculator',
  headers: {
    Authorization: 'Basic YWRtaW46ZW5yX01hc3Rlcl8xNQ==',
  },
})

export const get = (url, params, options) => {
  return axiosInstance.get(url + qs.stringify(params, { addQueryPrefix: true }), options)
}

export const post = (url, params, options) => {
  return axiosInstance.post(url, params, options)
}
