import React from 'react'
import PropTypes from 'prop-types'
import { FormBuilder, FormItem } from 'rc-form-pure'

import { SelectField } from '../../../../components/ui/form/fields/SelectField'
import { ReactComponent as EditIcon } from '../../../../components/ui/icons/edit.svg'
import { Button } from '../../../../components/ui/Button'

import { Container } from './Container'
import styles from './controls.module.less'

const voltageOptions = [
  { value: 'AVERAGE_VOLTAGE_1', label: 'СН1' },
  { value: 'AVERAGE_VOLTAGE_2', label: 'СН2' },
  { value: 'GENERATED_VOLTAGE', label: 'ГН' },
  { value: 'HIGH_VOLTAGE', label: 'ВН' },
  { value: 'LOW_VOLTAGE', label: 'НН' },
]
const tariffOptions = [
  { value: 'ONE_PART', label: 'ONE_PART' },
  { value: 'TWO_PART', label: 'TWO_PART' },
]

const FIELDS = {
  voltage: { fieldKey: 'voltage', label: 'Напряжение' },
  tariff: { fieldKey: 'tariff', label: 'Тариф' },
}

export const VoltageLevels = ({ isActive, setActiveSettings, transferPriceTable, onSubmit }) => {
  const handleSubmit = (values, errors) => {
    if (!errors) {
      onSubmit({ TransferPriceTable: { [values[FIELDS.voltage.fieldKey]]: values[FIELDS.tariff.fieldKey] } })
      setActiveSettings()
    }
  }
  const [voltageValue, tariffValue] = Object.entries(transferPriceTable)[0] || []
  const voltageValueLabel = voltageOptions.find(({ value }) => value === voltageValue)?.label
  const tariffValueLabel = tariffOptions.find(({ value }) => value === tariffValue)?.label

  return (
    <Container isActive={isActive}>
      <span className={styles.title} role="button" onClick={setActiveSettings}>
        Уровни напряжения <EditIcon className={styles.titleIcon} />
      </span>

      {isActive ? (
        <FormBuilder withForm onSubmit={handleSubmit}>
          <FormItem
            name={FIELDS.voltage.fieldKey}
            label={FIELDS.voltage.label}
            labelSize="small"
            options={voltageOptions}
            initialValue={voltageValue}
            component={SelectField}
          />

          <FormItem
            name={FIELDS.tariff.fieldKey}
            label={FIELDS.tariff.label}
            labelSize="small"
            options={tariffOptions}
            initialValue={tariffValue}
            component={SelectField}
          />

          <div className={styles.submitContainer}>
            <Button block type="primary" htmlType="submit" size="large" className="asd">
              Сохранить
            </Button>
          </div>
        </FormBuilder>
      ) : (
        <div className={styles.values}>
          <label className={styles.label}>{FIELDS.voltage.label}</label>
          <span className={styles.value}>{voltageValueLabel}</span>

          <label className={styles.label}>{FIELDS.tariff.label}</label>
          <span className={styles.value}>{tariffValueLabel}</span>
        </div>
      )}
    </Container>
  )
}

VoltageLevels.propTypes = {
  onSubmit: PropTypes.func.isRequired,
  isActive: PropTypes.bool,
  setActiveSettings: PropTypes.func.isRequired,
  transferPriceTable: PropTypes.shape({
    [voltageOptions[0].value]: PropTypes.oneOf([tariffOptions[0].value, tariffOptions[1].value]),
    [voltageOptions[1].value]: PropTypes.oneOf([tariffOptions[0].value, tariffOptions[1].value]),
    [voltageOptions[2].value]: PropTypes.oneOf([tariffOptions[0].value, tariffOptions[1].value]),
    [voltageOptions[3].value]: PropTypes.oneOf([tariffOptions[0].value, tariffOptions[1].value]),
    [voltageOptions[4].value]: PropTypes.oneOf([tariffOptions[0].value, tariffOptions[1].value]),
  }),
}

VoltageLevels.defaultProps = {
  transferPriceTable: {},
}
