import React from 'react'
import PropTypes from 'prop-types'
import { FormBuilder, FormItem, Validators } from 'rc-form-pure'

import { ReactComponent as EditIcon } from '../../../../components/ui/icons/edit.svg'
import { Button } from '../../../../components/ui/Button'
import { TextField } from '../../../../components/ui/form/fields/TextField'

import { Container } from './Container'
import styles from './controls.module.less'

const FIELDS = {
  name: { label: 'Наименование объекта', fieldKey: 'Name' },
}

export const Name = ({ isActive, setActiveSettings, name = '', onSubmit }) => {
  const handleSubmit = (values, errors) => {
    if (!errors) {
      onSubmit(values)
      setActiveSettings()
    }
  }

  return (
    <Container isActive={isActive}>
      <span className={styles.title} role="button" onClick={setActiveSettings}>
        Наименование <EditIcon className={styles.titleIcon} />
      </span>

      {isActive ? (
        <FormBuilder withForm onSubmit={handleSubmit}>
          <FormItem
            name={FIELDS.name.fieldKey}
            label={FIELDS.name.label}
            underline
            labelSize="small"
            initialValue={name}
            validate={Validators.required}
            component={TextField}
          />

          <div className={styles.submitContainer}>
            <Button block type="primary" htmlType="submit" size="large" className="asd">
              Сохранить
            </Button>
          </div>
        </FormBuilder>
      ) : (
        <div className={styles.values}>
          <label className={styles.label}>{FIELDS.name.label}</label>
          <span className={styles.value}>{name}</span>
        </div>
      )}
    </Container>
  )
}

Name.propTypes = {
  isActive: PropTypes.bool,
  setActiveSettings: PropTypes.func.isRequired,
  onSubmit: PropTypes.func.isRequired,
  name: PropTypes.string,
}
