import React from 'react'
import PropTypes from 'prop-types'
import { FormBuilder, FormItem } from 'rc-form-pure'

import { ReactComponent as EditIcon } from '../../../../components/ui/icons/edit.svg'
import { TextField } from '../../../../components/ui/form/fields/TextField'
import { SelectField } from '../../../../components/ui/form/fields/SelectField'
import { Button } from '../../../../components/ui/Button/Button'

import { Container } from './Container'
import styles from './controls.module.less'

const priceCategoryItems = [
  { value: 'THIRD', label: 'третья' },
  { value: 'FOURTH', label: 'четвёртая' },
  { value: 'FIFTH', label: 'пятая' },
  { value: 'SIXTH', label: 'шестая' },
]

const FIELDS = {
  supplierId: { label: 'ID поставщика', fieldKey: 'SupplierId' },
  priceCategory: { label: 'Ценовая категория', fieldKey: 'PriceCategory' },
}

export const RreSettings = ({ isActive, setActiveSettings, data, onSubmit }) => {
  const handleSubmit = (values, errors) => {
    if (!errors) {
      onSubmit(values)
      setActiveSettings()
    }
  }
  const supplierIdInitialValue = data[FIELDS.supplierId.fieldKey]
  const priceCategoryInitialValue = data[FIELDS.priceCategory.fieldKey]

  return (
    <Container isActive={isActive}>
      <span className={styles.title} role="button" onClick={setActiveSettings}>
        Настройки РРЭ <EditIcon className={styles.titleIcon} />
      </span>

      {isActive ? (
        <FormBuilder withForm onSubmit={handleSubmit}>
          <FormItem
            name={FIELDS.supplierId.fieldKey}
            label={FIELDS.supplierId.label}
            underline
            labelSize="small"
            initialValue={supplierIdInitialValue}
            component={TextField}
          />
          <FormItem
            name={FIELDS.priceCategory.fieldKey}
            label={FIELDS.priceCategory.label}
            labelSize="small"
            options={priceCategoryItems}
            initialValue={priceCategoryInitialValue}
            component={SelectField}
          />

          <div className={styles.submitContainer}>
            <Button block type="primary" size="large" htmlType="submit" className="asd">
              Сохранить
            </Button>
          </div>
        </FormBuilder>
      ) : (
        <div className={styles.values}>
          <label className={styles.label}>{FIELDS.supplierId.label}</label>
          <span className={styles.value}>{supplierIdInitialValue}</span>

          <label className={styles.label}>{FIELDS.priceCategory.label}</label>
          <span className={styles.value}>{priceCategoryInitialValue}</span>
        </div>
      )}
    </Container>
  )
}

RreSettings.propTypes = {
  onSubmit: PropTypes.func.isRequired,
  isActive: PropTypes.bool,
  setActiveSettings: PropTypes.func.isRequired,
  data: PropTypes.shape({
    [FIELDS.supplierId.fieldKey]: PropTypes.number,
    [FIELDS.priceCategory.fieldKey]: PropTypes.oneOf([
      priceCategoryItems[0].value,
      priceCategoryItems[1].value,
      priceCategoryItems[2].value,
      priceCategoryItems[3].value,
    ]),
  }),
}
