import React from 'react'
import PropTypes from 'prop-types'
import { FormBuilder, FormItem } from 'rc-form-pure'

import { ReactComponent as EditIcon } from '../../../../components/ui/icons/edit.svg'
import { SelectField } from '../../../../components/ui/form/fields/SelectField'
import { Button } from '../../../../components/ui/Button/Button'

import { Container } from './Container'
import styles from './controls.module.less'

const oremDataMethodOptions = [
  { value: 'AUTO', label: 'AUTO' },
  { value: 'MANUAL', label: 'MANUAL' },
]

const FIELDS = {
  oremDataMethod: { fieldKey: 'OremDataMethod', label: 'Способ получения данных' },
  // TODO: algorithmOrem key with capitalize
  algorithmOrem: { fieldKey: 'algorithmOrem', label: 'Алгоритм ОРЭМ' },
}

export const OremSettings = ({ isActive, setActiveSettings, data, onSubmit }) => {
  const handleSubmit = (values, errors) => {
    if (!errors) {
      onSubmit(values)
      setActiveSettings()
    }
  }

  const oremDataMethodInitialValue = data[FIELDS.oremDataMethod.fieldKey]
  const algorithmOremInitialValue = data[FIELDS.algorithmOrem.fieldKey]

  return (
    <Container isActive={isActive}>
      <span className={styles.title} role="button" onClick={setActiveSettings}>
        Настройки ОРЭМ <EditIcon className={styles.titleIcon} />
      </span>

      {isActive ? (
        <FormBuilder withForm onSubmit={handleSubmit}>
          <FormItem
            name={FIELDS.oremDataMethod.fieldKey}
            label={FIELDS.oremDataMethod.label}
            labelSize="small"
            options={oremDataMethodOptions}
            initialValue={oremDataMethodInitialValue}
            component={SelectField}
          />

          <FormItem
            name={FIELDS.algorithmOrem.fieldKey}
            label={FIELDS.algorithmOrem.label}
            labelSize="small"
            initialValue={algorithmOremInitialValue}
            component={SelectField}
          />

          <div className={styles.submitContainer}>
            <Button block type="primary" size="large" htmlType="submit" className="asd">
              Сохранить
            </Button>
          </div>
        </FormBuilder>
      ) : (
        <div className={styles.values}>
          <label className={styles.label}>{FIELDS.oremDataMethod.label}</label>
          <span className={styles.value}>{oremDataMethodInitialValue}</span>

          <label className={styles.label}>{FIELDS.algorithmOrem.fieldKey}</label>
          <span className={styles.value}>{algorithmOremInitialValue}</span>
        </div>
      )}
    </Container>
  )
}

OremSettings.propTypes = {
  onSubmit: PropTypes.func.isRequired,
  isActive: PropTypes.bool,
  setActiveSettings: PropTypes.func.isRequired,
  data: PropTypes.shape({
    [FIELDS.oremDataMethod.fieldKey]: PropTypes.oneOf([oremDataMethodOptions[0].value, oremDataMethodOptions[1].value]),
    [FIELDS.algorithmOrem.fieldKey]: PropTypes.string,
  }),
}
